.dropdown-toggle { outline: 0; }

.nav-link,
.nav-link:visited {
    color: inherit; /* Or any color you prefer */
}

.fixed-sidebar {
    position: fixed;
    top: 0;
    left: 0;
    width: 200px;
    height: 100vh;
    background-color: #f8f9fa; /* Или любой другой цвет фона */
    overflow-y: auto; /* Добавьте, если содержимое боковой панели может быть длинным */
    transition: transform 0.3s ease; /* Добавляем плавный переход */
}

.sidebar-hidden {
    transform: translateX(-100%); /* Скрываем боковую панель */
}

 @media (prefers-color-scheme: dark) {
    .bi {
        filter: invert(100%);
    }
}